export default {
	data() {
		return {
			data: this.dataList
		};
	},
	props: [
		'dataList',
	],
	created() {
	},
	watch: {
		dataList: {
			handler(a, b) {
				console.log(a, b, '-----------dataList----------');
				this.data = a;
			},
			deep: true
		},
	},
	mounted() {
	},
	methods: {
		
	}
};
